// src/js/home.js

import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

import {
    fetchProductsByCategory,
    fetchAllProducts,
    fetchProductById,
    searchProductsByName
} from './js/products-api.js';
import {   
    createMarkupProducts,
    clearMarkupProducts,
    initPage,
    showLoadMoreBtn,
    hideLoadMoreBtn,
    showLoader,
    hideLoader,
    addLoadMoreProducts,
    renderCardsFromStorage,
    showPageLoader,
    hidePageLoader
} from "./js/render-function";
import { refs } from './js/refs.js';
import { cards, saveCardsToStorage, loadCardsFromStorage } from './js/storage.js';
import { openModal, closeModal } from './js/modal.js';


document.addEventListener('DOMContentLoaded', async () => {
  showPageLoader();

  // –î–æ—á–µ–∫–∞–π—Å—è, –ø–æ–∫–∏ DOM –Ω–∞–º–∞–ª—é—î –ª–æ–∞–¥–µ—Ä
  await new Promise(resolve => setTimeout(resolve, 100));

//   loadCardsFromStorage();
//   renderCardsFromStorage();
  initPage();

  hidePageLoader();

  // –ü–æ–∫–∞–∑—É—î–º–æ –æ—Å–Ω–æ–≤–Ω–∏–π –∫–æ–Ω—Ç–µ–Ω—Ç –ø—ñ—Å–ª—è –∑–Ω–∏–∫–Ω–µ–Ω–Ω—è –ª–æ–∞–¥–µ—Ä–∞
    document.getElementById('content').classList.remove('is-hidden');
    
});


// –ó–º—ñ–Ω–Ω–∞ –¥–ª—è –∑–±–µ—Ä–µ–∂–µ–Ω–Ω—è –ø–æ—Ç–æ—á–Ω–æ–≥–æ –ø–æ—à—É–∫–æ–≤–æ–≥–æ –∑–∞–ø–∏—Ç—É
let currentQuery = '';

// –ó–º—ñ–Ω–Ω–∞ –¥–ª—è –≤—ñ–¥—Å—Ç–µ–∂–µ–Ω–Ω—è –ø–æ—Ç–æ—á–Ω–æ—ó —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –ø—Ä–∏ –ø–∞–≥—ñ–Ω–∞—Ü—ñ—ó
let currentPage = 1;

// –ö—ñ–ª—å–∫—ñ—Å—Ç—å –ø—Ä–æ–¥—É–∫—Ç—ñ–≤, —è–∫—ñ –±—É–¥—É—Ç—å –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω—ñ –Ω–∞ –æ–¥–Ω—É —Å—Ç–æ—Ä—ñ–Ω–∫—É
const IMAGES_PER_PAGE = 12;

 

// –î–æ–¥–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó –Ω–∞ –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó
refs.categoriesListEl.addEventListener('click', async (e) => {
    if (e.target === e.currentTarget) return
    // –û—Ç—Ä–∏–º—É—î–º–æ ID –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó –∑ –∞—Ç—Ä–∏–±—É—Ç–∞ data-id
    const categoryName = e.target.dataset.category;

    // –Ø–∫—â–æ ID –Ω–µ –≤–∫–∞–∑–∞–Ω–æ ‚Äî –≤–∏—Ö–æ–¥–∏–º–æ –∑ —Ñ—É–Ω–∫—Ü—ñ—ó
    if (!categoryName) return;

  // –î–æ–¥–∞—î–º–æ/–∑–Ω—ñ–º–∞—î–º–æ –∫–ª–∞—Å –∞–∫—Ç–∏–≤–Ω–æ—ó –∫–Ω–æ–ø–∫–∏
    const categoryButtons = refs.categoriesListEl.querySelectorAll('.categories__btn');
    categoryButtons.forEach(btn => btn.classList.remove('categories__btn--active'));
    e.target.classList.add('categories__btn--active');
    // –°–∫–∏–¥–∞—î–º–æ –Ω–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –Ω–∞ 1
    currentPage = 1;

    // –Ø–∫—â–æ –∑–∞–ø–∏—Ç –≤–∞–ª—ñ–¥–Ω–∏–π:
    clearMarkupProducts();  
    hideLoadMoreBtn();
    showLoader();
    try {
        const products = await fetchProductsByCategory(categoryName);

        if (products.length === 0) {
            iziToast.error({
                title: 'Error',
                message: 'No products found in this category.',
                position: 'topCenter',
            });
            hideLoader();
            return;
        }
            cards.length = 0;
            cards.push(...products);
            saveCardsToStorage();


         // –Ø–∫—â–æ –ø—Ä–æ–¥—É–∫—Ç–∏ –∑–Ω–∞–π–¥–µ–Ω—ñ ‚Äî —Å—Ç–≤–æ—Ä—é—î–º–æ HTML-—Ä–æ–∑–º—ñ—Ç–∫—É
        const markup = createMarkupProducts(products);

        // –í—Å—Ç–∞–≤–ª—è—î–º–æ –∑–≥–µ–Ω–µ—Ä–æ–≤–∞–Ω—É —Ä–æ–∑–º—ñ—Ç–∫—É –≤ DOM
        refs.productsListEl.insertAdjacentHTML('beforeend', markup);

        // –ü–æ–∫–∞–∑—É—î–º–æ –∫–Ω–æ–ø–∫—É "Load More" –¥–ª—è –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –Ω–∞—Å—Ç—É–ø–Ω–æ—ó —Å—Ç–æ—Ä—ñ–Ω–∫–∏
        showLoadMoreBtn();

    } catch (error) {
        console.error('Error fetching products:', error.message);
    } finally {
        hideLoader(); // –•–æ–≤–∞—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è
    }
});
// –û–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó submit —Ñ–æ—Ä–º–∏ –ø–æ—à—É–∫—É
refs.formEl.addEventListener('submit', async (e) => {
  e.preventDefault(); // –°–∫–∞—Å–æ–≤—É—î–º–æ –ø–µ—Ä–µ–∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –ø—Ä–∏ –≤—ñ–¥–ø—Ä–∞–≤—Ü—ñ —Ñ–æ—Ä–º–∏

  const query = refs.inputEl.value.trim(); // –û—Ç—Ä–∏–º—É—î–º–æ —Ç–∞ –æ–±—Ä—ñ–∑–∞—î–º–æ –∑–Ω–∞—á–µ–Ω–Ω—è –∑ –ø–æ–ª—è –≤–≤–æ–¥—É

  // –Ø–∫—â–æ –ø–æ–ª–µ –ø–æ—Ä–æ–∂–Ω—î ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ø–æ–º–∏–ª–∫—É
  if (!query) {
    iziToast.error({
      title: 'Error',
      message: 'Please enter a search query.',
      position: 'topCenter',
    });
    return; // –í–∏—Ö—ñ–¥ —ñ–∑ —Ñ—É–Ω–∫—Ü—ñ—ó
  }

  clearMarkupProducts(); // –û—á–∏—â–∞—î–º–æ –ø–æ–ø–µ—Ä–µ–¥–Ω—ñ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∏ –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω—Ü—ñ
  hideLoadMoreBtn();     // –ü—Ä–∏—Ö–æ–≤—É—î–º–æ –∫–Ω–æ–ø–∫—É "Load More"
  showLoader();          // –ü–æ–∫–∞–∑—É—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è (—Å–ø—ñ–Ω–µ—Ä)

  try {
    // –®—É–∫–∞—î–º–æ —Ç–æ–≤–∞—Ä–∏ –∑–∞ –≤–≤–µ–¥–µ–Ω–∏–º –∑–∞–ø–∏—Ç–æ–º
    const products = await searchProductsByName(query);

    // –Ø–∫—â–æ —Ç–æ–≤–∞—Ä–∏ –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ ‚Äî –ø–æ–≤—ñ–¥–æ–º–ª—è—î–º–æ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞
    if (!products || products.length === 0) {
      iziToast.error({
        title: 'Error',
        message: 'No products found.',
        position: 'topCenter',
      });
      return;
    }

    // –û—á–∏—â–∞—î–º–æ –º–∞—Å–∏–≤ –∫–∞—Ä—Ç–æ–∫ —ñ –¥–æ–¥–∞—î–º–æ –Ω–æ–≤—ñ
    cards.length = 0;
    cards.push(...products);
    saveCardsToStorage(); // –ó–±–µ—Ä—ñ–≥–∞—î–º–æ –∫–∞—Ä—Ç–∫–∏ —É localStorage

    // –°—Ç–≤–æ—Ä—é—î–º–æ HTML-—Ä–æ–∑–º—ñ—Ç–∫—É –∑ –æ—Ç—Ä–∏–º–∞–Ω–∏—Ö —Ç–æ–≤–∞—Ä—ñ–≤
    const markup = createMarkupProducts(products);

    // –í—Å—Ç–∞–≤–ª—è—î–º–æ —Ä–æ–∑–º—ñ—Ç–∫—É –≤ DOM (–≤ –∫—ñ–Ω–µ—Ü—å —Å–ø–∏—Å–∫—É)
    refs.productsListEl.insertAdjacentHTML('beforeend', markup);

    // –ü–æ–∫–∞–∑—É—î–º–æ –∫–Ω–æ–ø–∫—É "Load More"
    showLoadMoreBtn();

  } catch (error) {
    // –Ø–∫—â–æ —Å—Ç–∞–ª–∞—Å—è –ø–æ–º–∏–ª–∫–∞ ‚Äî –≤–∏–≤–æ–¥–∏–º–æ —ó—ó –≤ –∫–æ–Ω—Å–æ–ª—å —ñ –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
    console.error('Search error:', error.message);
    iziToast.error({
      title: 'Error',
      message: 'Something went wrong. Please try again.',
      position: 'topCenter',
    });
  } finally {
    // –ó–∞–≤–∂–¥–∏ —Ö–æ–≤–∞—î–º–æ –ª–æ–∞–¥–µ—Ä —ñ –æ—á–∏—â–∞—î–º–æ –ø–æ–ª–µ –≤–≤–æ–¥—É
    hideLoader();
    refs.formEl.reset();
  }
});

// –î–æ–¥–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó –Ω–∞ –∫–Ω–æ–ø–∫—É "Load More"
refs.loadMoreBtn.addEventListener('click', async () => {
  // –ó–±—ñ–ª—å—à—É—î–º–æ –Ω–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏ ‚Äî —Ü–µ –¥–æ–∑–≤–æ–ª—è—î –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ –Ω–∞—Å—Ç—É–ø–Ω—É "–ø–∞—Ä—Ç—ñ—é" —Ç–æ–≤–∞—Ä—ñ–≤
  currentPage += 1;

  // –ü–æ–∫–∞–∑—É—î–º–æ —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è
    showLoader();
    hideLoadMoreBtn();   

  // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ –º—ñ–Ω—ñ–º–∞–ª—å–Ω–∏–π —á–∞—Å –ø–æ–∫–∞–∑—É –ª–æ–∞–¥–µ—Ä–∞ (2 —Å–µ–∫—É–Ω–¥–∏)
  const startTime = Date.now();
  const MIN_LOADER_TIME = 2000;

  try {
    // –û—Ç—Ä–∏–º—É—î–º–æ –ø—Ä–æ–¥—É–∫—Ç–∏ –∑–∞ –ø–æ—Ç–æ—á–Ω–∏–º –∑–∞–ø–∏—Ç–æ–º —ñ —Å—Ç–æ—Ä—ñ–Ω–∫–æ—é
    const products = await fetchAllProducts(currentQuery, currentPage);

    // –Ø–∫—â–æ –Ω–æ–≤–∏—Ö –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –Ω–µ–º–∞—î, –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è —ñ —Ö–æ–≤–∞—î–º–æ –∫–Ω–æ–ø–∫—É
    if (products.length === 0) {
      iziToast.error({
        title: 'Error',
        message: 'No more products found.',
        position: 'topCenter',
      });
      hideLoadMoreBtn();
      return;
    }

    // –†–µ–Ω–¥–µ—Ä–∏–º–æ –ø—Ä–æ–¥—É–∫—Ç–∏ –¥–æ DOM (—Ü—è —Ñ—É–Ω–∫—Ü—ñ—è –º–∞—î –≤—Å—Ç–∞–≤–∏—Ç–∏ –Ω–æ–≤—É –ø–æ—Ä—Ü—ñ—é –ø—Ä–æ–¥—É–∫—Ç—ñ–≤)
    addLoadMoreProducts(products);

  } catch (error) {
    // –Ø–∫—â–æ —Å—Ç–∞–ª–∞—Å—è –ø–æ–º–∏–ª–∫–∞ ‚Äî –≤–∏–≤–æ–¥–∏–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
    iziToast.error({
      title: 'Error',
      message: 'Failed to load products.',
      position: 'topCenter',
    });
  } finally {
    // –ó–∞–±–µ–∑–ø–µ—á—É—î–º–æ –º—ñ–Ω—ñ–º–∞–ª—å–Ω–∏–π —á–∞—Å –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –ª–æ–∞–¥–µ—Ä–∞
    const elapsedTime = Date.now() - startTime;
    const remainingTime = MIN_LOADER_TIME - elapsedTime;

    if (remainingTime > 0) {
        setTimeout(() => {
        showLoadMoreBtn();
        hideLoader();
      }, remainingTime);
    } else {
        hideLoader();
        showLoadMoreBtn();
    }
  }
});

// –î–æ–¥–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó –Ω–∞ –∫–Ω–æ–ø–∫—É –æ—á–∏—â–µ–Ω–Ω—è –ø–æ—à—É–∫—É
refs.clearSearchBtnEl.addEventListener('click', () => {
    // –û—á–∏—â–∞—î–º–æ –ø–æ–ª–µ –≤–≤–æ–¥—É
    refs.inputEl.value = '';

    // // –û—á–∏—â–∞—î–º–æ —Å–ø–∏—Å–æ–∫ –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
    // clearMarkupProducts();

    // // –•–æ–≤–∞—î–º–æ –∫–Ω–æ–ø–∫—É "Load More"
    // hideLoadMoreBtn();

    // –°–∫–∏–¥–∞—î–º–æ –Ω–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –Ω–∞ 1
    currentPage = 1;

    // –ü–æ–∫–∞–∑—É—î–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –æ—á–∏—â–µ–Ω–Ω—è –ø–æ—à—É–∫—É
    iziToast.info({
        title: 'Info',
        message: 'Search cleared.',
        position: 'topCenter',
    });
});

// –î–æ–¥–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó –¥–ª—è –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞ –∑ –¥–µ—Ç–∞–ª—è–º–∏ –ø—Ä–æ–¥—É–∫—Ç—É
refs.productsListEl.addEventListener('click', async (e) => {
    const productCard = e.target.closest('.products__item');
    if (!productCard) return;

    const productId = productCard.dataset.id;
    if (!productId) return;

    try {
        showLoader();
        const product = await fetchProductById(productId);
        openModal(product); // –ü–æ–∫–∞–∑ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞ –∑ –¥–µ—Ç–∞–ª—è–º–∏
    } catch (error) {
        iziToast.error({
            title: 'Error',
            message: 'Failed to load product details.',
            position: 'topCenter',
        });
        console.error('Error loading product:', error);
    } finally {
        hideLoader();
    }
});

